import React, { useState, useEffect, useCallback } from "react";

import Message from "./Message";
import "./Message-List.css";
import ChatSocketServer from "./utils/chatSocketServer";
import ChatHttpServer from "./utils/chatHttpServer";
import Selected from "./Selected";

const MessageList = (props) => {
  const [userId] = useState(
    JSON.parse(localStorage.getItem("posts"))?.posts?.profile[0]._id
  );

  const { selectedChat, messageList, setMessageList } = props;

  const [loadingMessages, setLoadingMessages] = useState(false);
  const [offsetIndex, setOffsetIndex] = useState(0);

  useEffect(() => {
    setLoadingMessages(true);
    const getMessages = async () => {
      if (selectedChat.chatId) {
        const messageResponse = await ChatHttpServer.getMessages(
          selectedChat.chatId,
          offsetIndex
        );
        if (!messageResponse.error) {
          var responses = messageResponse.reverse();
          setMessageList([...responses, ...messageList]);
        } else {
          alert("Unable to fetch messages");
        }
      }

      setLoadingMessages(false);
    };

    getMessages();
  }, [selectedChat, offsetIndex]);

  const handleMessageAdd = useCallback(
    (data) => {
      if (selectedChat.chatId === data.chatId) {
        setMessageList([...messageList, data]);
      }
    },
    [messageList, selectedChat, setMessageList]
  );

  useEffect(() => {
    ChatSocketServer.eventEmitter.on("add-message-response", handleMessageAdd);
    return () =>
      ChatSocketServer.eventEmitter.removeListener(
        "add-message-response",
        handleMessageAdd
      );
  }, [handleMessageAdd]);

  const messageItems = messageList.map((message) => {
    var isMyMessage =
      JSON.stringify(message.senderId) === JSON.stringify(userId);

    if (message.state !== "READ" && !isMyMessage) {
      ChatSocketServer.updateMessage({
        ...message,
        state: "READ",
        chatId: message.chatId,
      });
    }

    console.log(messageList);

    return (
      <Message
        timeStamp={message.createdAt}
        id={message._id}
        key={message._id}
        state={message.state}
        isMyMessage={isMyMessage}
        message={message.message}
        file={message.files[0]}
      />
    );
  });

  return (
    <div id="chat-message-list">
      {props.selectedChat.chatId && (
        <div>
          {loadingMessages ? (
            <div>Fetching messages</div>
          ) : (
            <div>
              <button onClick={() => setOffsetIndex(offsetIndex + 1)}>
                Get older messages
              </button>
            </div>
          )}
          <div>{messageItems}</div>
        </div>
      )}
    </div>
  );
};

export default MessageList;
